'use client';

import { useState } from 'react';
import '../ai-studio.css'; // Import the new CSS file

// Placeholder icons - in a real app, you'd use an icon library like react-icons
const ChatIcon = () => <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M8 12h.01M12 12h.01M16 12h.01M21 12c0 4.418-4.03 8-9 8a9.863 9.863 0 01-4.255-.949L3 20l1.395-3.72C3.512 15.042 3 13.574 3 12c0-4.418 4.03-8 9-8s9 3.582 9 8z"></path></svg>;
const StreamIcon = () => <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M5 12h14M5 12a2 2 0 01-2-2V6a2 2 0 012-2h14a2 2 0 012 2v4a2 2 0 01-2 2M5 12a2 2 0 00-2 2v4a2 2 0 002 2h14a2 2 0 002-2v-4a2 2 0 00-2-2"></path></svg>;
const GenerateMediaIcon = () => <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l-1-1m6-3a2 2 0 012 2v10a2 2 0 01-2 2H5a2 2 0 01-2-2V8a2 2 0 012-2h3l2-3h4l2 3h3z"></path></svg>;
const BuildIcon = () => <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M10.325 4.317c.426-1.756 2.924-1.756 3.35 0a1.724 1.724 0 002.573 1.066c1.543-.94 3.31.826 2.37 2.37a1.724 1.724 0 001.065 2.572c1.756.426 1.756 2.924 0 3.35a1.724 1.724 0 00-1.066 2.573c.94 1.543-.826 3.31-2.37 2.37a1.724 1.724 0 00-2.572 1.065c-.426 1.756-2.924 1.756-3.35 0a1.724 1.724 0 00-2.573-1.066c-1.543.94-3.31-.826-2.37-2.37a1.724 1.724 0 00-1.065-2.572c-1.756-.426-1.756-2.924 0-3.35a1.724 1.724 0 001.066-2.573c-.94-1.543.826-3.31 2.37-2.37.996.608 2.296.07 2.572-1.065z"></path><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M15 12a3 3 0 11-6 0 3 3 0 016 0z"></path></svg>;
const HistoryIcon = () => <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z"></path></svg>;
const EnableSavingIcon = () => <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M5 13l4 4L19 7"></path></svg>;

export default function AIStudioHome() {
  const [prompt, setPrompt] = useState('');

  return (
    <div className="flex h-screen bg-[#121212] text-gray-300 font-sans">
      {/* Sidebar */}
      <aside className="w-64 bg-[#1e1e1e] p-4 flex flex-col">
        <nav className="flex-1 space-y-2">
          <a href="#" className="flex items-center space-x-3 px-3 py-2 bg-[#2d2d2d] rounded-md text-white font-medium">
            <ChatIcon />
            <span className="text-white">Chat</span>
          </a>
          <a href="#" className="flex items-center space-x-3 px-3 py-2 rounded-md hover:bg-[#2d2d2d]">
            <StreamIcon />
            <span className="text-gray-300">Stream</span>
          </a>
          <a href="#" className="flex items-center space-x-3 px-3 py-2 rounded-md hover:bg-[#2d2d2d]">
            <GenerateMediaIcon />
            <span className="text-gray-300">Generate Media</span>
          </a>
          <a href="#" className="flex items-center space-x-3 px-3 py-2 rounded-md hover:bg-[#2d2d2d]">
            <BuildIcon />
            <span className="text-gray-300">Build</span>
          </a>
          <a href="#" className="flex items-center space-x-3 px-3 py-2 rounded-md hover:bg-[#2d2d2d]">
            <HistoryIcon />
            <span className="text-gray-300">History</span>
          </a>
        </nav>
        <div className="mt-auto">
          <a href="#" className="flex items-center space-x-3 px-3 py-2 rounded-md hover:bg-[#2d2d2d]">
            <EnableSavingIcon />
            <span className="text-gray-300">Enable saving</span>
          </a>
        </div>
      </aside>

      {/* Main Content */}
      <main className="flex-1 flex flex-col overflow-hidden">
        {/* Header */}
        <header className="flex items-center justify-between p-4 border-b border-[#2d2d2d]">
          <div>
            <h1 className="text-lg font-semibold text-white">Untitled prompt</h1>
          </div>
          <div className="flex items-center space-x-4">
            {/* Placeholder for top-right icons */}
            <button className="text-gray-400 hover:text-white"><svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M8 7v8a2 2 0 002 2h4M8 7a2 2 0 012-2h4a2 2 0 012 2v8a2 2 0 01-2 2h-4a2 2 0 01-2-2z"></path></svg></button>
            <button className="text-gray-400 hover:text-white"><svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M10 20l4-16m4 4l4 4-4 4M6 16l-4-4 4-4"></path></svg></button>
            <button className="text-gray-400 hover:text-white"><svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M8.684 13.342C8.886 12.938 9 12.482 9 12s-.114-.938-.316-1.342m0 2.684a3 3 0 110-2.684m0 2.684l6.632 3.316m-6.632-6.002l6.632-3.316m0 0a3 3 0 105.367-2.684 3 3 0 00-5.367 2.684zm0 9.368a3 3 0 105.367 2.684 3 3 0 00-5.367-2.684z"></path></svg></button>
            <button className="text-gray-400 hover:text-white"><svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 16v1a3 3 0 003 3h10a3 3 0 003-3v-1m-4-8l-4-4m0 0L8 8m4-4v12"></path></svg></button>
            <button className="text-gray-400 hover:text-white"><svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M15 19l-7-7 7-7"></path></svg></button>
            <button className="text-gray-400 hover:text-white"><svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9 5l7 7-7 7"></path></svg></button>
            <button className="text-gray-400 hover:text-white"><svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 5v.01M12 12v.01M12 19v.01M12 6a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2z"></path></svg></button>
          </div>
        </header>

        {/* Content Area */}
        <div className="flex-1 flex flex-col items-center justify-center p-8 overflow-y-auto ai-studio-scrollbar">
          <h2 className="text-4xl font-bold text-blue-400 mb-4">Welcome to AI Studio</h2>
          
          <div className="w-full max-w-2xl my-8">
            <div className="relative">
              <input
                type="text"
                value={prompt}
                onChange={(e) => setPrompt(e.target.value)}
                placeholder="Explain the probability of rolling two dice and getting 7"
                className="w-full bg-[#2d2d2d] border border-gray-700 rounded-full py-3 pl-6 pr-28 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-blue-500"
              />
              <div className="absolute inset-y-0 right-0 flex items-center pr-2">
                <button className="text-gray-400 hover:text-white p-2"><svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 9v3m0 0v3m0-3h3m-3 0H9m12 0a9 9 0 11-18 0 9 9 0 0118 0z"></path></svg></button>
                <button className="bg-[#2d2d2d] text-white rounded-full px-4 py-2 text-sm font-medium hover:bg-gray-600">
                  Run
                </button>
              </div>
            </div>
          </div>

          <div className="w-full max-w-3xl">
            <h3 className="text-lg font-semibold text-gray-400 mb-4">What's new</h3>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div className="bg-[#2d2d2d] p-4 rounded-lg border border-gray-700 hover:bg-gray-700 cursor-pointer">
                <h4 className="font-bold text-white">URL context tool</h4>
                <p className="text-gray-400 text-sm">Fetch information from web links</p>
              </div>
              <div className="bg-[#2d2d2d] p-4 rounded-lg border border-gray-700 hover:bg-gray-700 cursor-pointer">
                <h4 className="font-bold text-white">Native speech generation</h4>
                <p className="text-gray-400 text-sm">Generate high quality text to speech with Gemini</p>
              </div>
              <div className="bg-[#2d2d2d] p-4 rounded-lg border border-gray-700 hover:bg-gray-700 cursor-pointer">
                <h4 className="font-bold text-white">Live audio-to-audio dialog</h4>
                <p className="text-gray-400 text-sm">Try Gemini's natural, real-time dialog with audio and video inputs</p>
              </div>
              <div className="bg-[#2d2d2d] p-4 rounded-lg border border-gray-700 hover:bg-gray-700 cursor-pointer">
                <h4 className="font-bold text-white">Native image generation</h4>
                <p className="text-gray-400 text-sm">Interleaved text-and-image generation with Gemini 2.0 Flash</p>
              </div>
            </div>
          </div>
        </div>
      </main>
    </div>
  );
}
